ymaps.ready(init);

function init(){
  const mapElement = document.getElementById("map");
  const mapOptions = {
    zoom: 15,
  };

  //    
  const screenWidth = window.innerWidth;

  if (screenWidth <= 1024) {
    mapOptions.center = [52.225261, 104.111481];
  } else {
    mapOptions.center = [52.226083, 104.096502];
  }

  const map = new ymaps.Map(mapElement, mapOptions);

  const placemark = new ymaps.Placemark(
    [52.225261, 104.111481],
    {
      hintContent: '  , 56',
      balloonContent: '  , 56'
    },
    {
      preset: 'islands#icon', //  
      iconColor: '#D0043C' //  
    }
  );

  map.geoObjects.add(placemark);
}

